@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap);
:root{--nav-width: 200px;--nav-bg: #222;--title-size: 1.1rem;--primary: #66e8ff}@media (max-width: 600px){:root{--nav-width: 120px;--title-size: 0.8rem}}body{margin:0;padding:0;font-family:'Lato', sans-serif;background:#fff;color:black;scroll-behavior:smooth}main{width:calc(100% - 200px - 2rem);width:calc(100% - var(--nav-width) - 2rem);margin-left:200px;margin-left:var(--nav-width);padding:1rem}h1,h2,h3,p,ul,li{margin:0;padding:0}p,ul{padding-bottom:1rem}ul{margin-left:1rem}a{text-decoration:none;color:#4013a0}table{text-align:left;font-weight:normal;border-collapse:collapse;margin-bottom:1rem}table,th,td{border:1px solid black}th,td{padding:0.5rem}tr:nth-child(even){background-color:#f2f2f2}th{background-color:#9fffbf}

nav{position:fixed;top:0;left:0;height:100%;width:var(--nav-width);background:var(--nav-bg);color:white;box-shadow:0px 0 8px 2px rgba(0,0,0,0.8)}nav .title{text-align:center;font-size:var(--title-size);padding:1rem;color:var(--primary);border-bottom:2px solid white}nav ul{list-style:none;margin:0;margin-top:1rem;padding:0}nav ul li{width:100%;height:2rem;background:transparent;color:white;font-weight:400}nav ul li a{width:calc(100% - 1rem);height:100%;display:flex;align-items:center;text-decoration:none;color:white;padding-left:1rem}nav ul li a:hover{color:#ccc}nav .links{margin-bottom:1rem}nav .subject-links{margin-top:1rem;border-top:1px solid white}.active{background:var(--primary);color:black}.active:hover{color:#838383}

.subjects .links{list-style:none;margin-left:0}

*{line-height:1.3rem}h2{margin-top:1rem}p{padding-bottom:0}p,h3,h4{margin-top:0;margin-bottom:0}h4{font-size:1.2rem;margin-bottom:0.5rem}ul{padding-bottom:0}ul li a{font-weight:700}.heading{font-weight:700;margin-top:0.5rem}.sub-heading{font-weight:700}.topic{margin-bottom:1rem}

